.contacts
  &__block 
    padding: 20px
    background-color: $white
    border-radius: $border-radius
    position: relative
    overflow: hidden
    &:before 
      content: ''
      position: absolute
      bottom: -20px
      right: -30px
      height: 100px
      width: 108px
      background-image: url("data:image/svg+xml;base64,")
      background-size: cover
      background-position: center
      background-repeat: no-repeat
      opacity: .4
    &-content
      margin-bottom: 20px
  &__name
    font-weight: 500
  &__link
    display: block
    color: $blueDark
    margin-top: 10px
    transition: $transitionAll
    font-weight: 400
    &:hover 
      color: $blue
      transition: $transitionAll
  &__text
    color: $black
    font-weight: 300
    margin-top: 10px
  
  &.grid.grid--3
    @include media_768
      grid-template-columns: repeat(1, 1fr)