.basket
  display: flex

  @include media_992
    flex-direction: column

  &-body
    width: 70.97%

    @include media_1200
      width: 70%

    @include media_992
      width: 100%

  &-cheque
    width: 27.8%
    margin-left: 15px

    @include media_1200
      width: 30%

    @include media_992
      width: 100%
      margin-left: 0
      margin-top: 20px

    &__inner
      border-radius: $border-radius
      background-color: $white
      width: 100%
      min-height: 300px
      position: sticky
      top: 15px
      padding: 15px
      display: flex
      flex-direction: column
      justify-content: space-between

    &__title
      font-size: 1.25rem
      font-weight: 600

    &__count
      font-size: 1.125rem
      font-weight: 400
      margin-top: 15px
      white-space: nowrap
    
    &__list
      margin-top: 15px

    &__item
      position: relative
      font-size: 0.875rem
      font-weight: 400
      padding: 3px 5px 3px 20px
      display: flex
      align-items: center
      justify-content: space-between
      border-radius: 2px

      &::before
        content: ''
        position: absolute
        top: 7px
        left: 5px
        height: 10px
        width: 10px
        background-image: url("data:image/svg+xml;base64,")
        background-position: center
        background-size: cover

      &-count
        min-width: 50px

      &:nth-child(2n-1)
        background-color: #e4f5fe
      
      &-name
        padding-right: 15px
    
    &__bottom
      margin-top: 30px
      padding-top: 10px
      border-top: 1px solid #D9E9F3

    &__total
      display: flex
      align-items: center
      justify-content: space-between

      &-text
        font-size: 1.25rem
        font-weight: 600
      &-sum
        font-size: 1.25rem
        font-weight: 600

    &__btns
      margin-top: 30px

    &__btn
      @include media_576
        width: 100%
        text-align: center

  &-order
    display: flex

    @include media_768
      flex-direction: column

    &__left
      width: 40%
      padding-right: 15px

      @include media_768
        width: 100%
    
    &__text 
      color: $white

    &__right
      width: 60%

      @include media_768
        width: 100%

  &-form
    &__row
      margin-top: 10px
      position: relative
      &:first-child
        margin-top: 0
      .input
        background-color: rgba(248, 251, 255, .65)
      .textarea
        background-color: rgba(248, 251, 255, .65)
    &__btns
      display: flex
      justify-content: right

      button
        @include media_576
          width: 100%