.services-list
  display: grid
  grid-template-columns: repeat(3, 1fr)
  grid-column-gap: 15px
  grid-row-gap: 15px
  
.services-link
  display: flex
  background-color: $white
  padding: 15px 40px 15px 15px
  position: relative
  overflow: hidden
  border-radius: $border-radius

  &:hover
    .services-link__bg
      opacity: .3
      transition: $transitionAll

    .services-link__arrow
      svg
        path
          stroke: $blueDark
          transition: $transitionAll

  &__icon
    max-width: 90px
    z-index: 2

    img
      width: 100%

  &__name
    padding-left: 15px
    margin-top: auto
    font-size: 1rem
    color: $black
    text-transform: uppercase

  &__arrow
    position: absolute
    top: 15px
    right: 15px
    height: 30px
    width: 17px
    svg
      path
        transition: $transitionAll

  &__bg
    position: absolute
    bottom: -30px
    right: -40px
    height: 102px
    width: 110px
    background-image: url("data:image/svg+xml;base64,")
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    opacity: 0
    z-index: 1
    transition: $transitionAll